}

/*START Range*/
.range {
  -webkit-appearance: none;
  -moz-appearance: none;
}


input[type=range].brightness::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  background: rgb(83, 83, 83);
  background: -moz-linear-gradient(45deg, rgb(255, 255, 255) 0%,  rgb(32, 32, 32) 100%);
  background: -webkit-gradient(left bottom, right top, color-stop(0%, rgb(255, 255, 255)) , color-stop(100%,rgb(32, 32, 32)));
  background: -webkit-linear-gradient(45deg, rgb(255, 255, 255) 0%,  rgb(32, 32, 32) 100%);
  background: -o-linear-gradient(45deg, rgb(255, 255, 255) 0%,  rgb(32, 32, 32) 100%);
  background: -ms-linear-gradient(45deg, rgb(255, 255, 255) 0%,  rgb(32, 32, 32) 100%);
  background: linear-gradient(45deg, rgb(255, 255, 255) 0%,  rgb(32, 32, 32) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#3bade3 ', endColorstr='#ff357f ', GradientType=1 );
  height: 2px;
}

