input[type=range]:focus {
  outline: none;
}

input[type=range]::-moz-range-track {
  -moz-appearance: none;
  background: rgb(83, 83, 83);
  background: -moz-linear-gradient(45deg, rgb(83, 83, 83) 0%, rgba(247, 19, 19) 100%);
  background: -webkit-gradient(left bottom, right top, color-stop(0%, rgb(83, 83, 83)), color-stop(100%, rgba(247, 19, 19)));
  background: -webkit-linear-gradient(45deg, rgb(83, 83, 83) 0%, rgba(247, 19, 19) 100%);
  background: -o-linear-gradient(45deg, rgb(83, 83, 83) 0%, rgba(247, 19, 19) 100%);
  background: -ms-linear-gradient(45deg, rgb(83, 83, 83) 0%, rgba(247, 19, 19) 100%);
  background: linear-gradient(45deg, rgb(83, 83, 83) 0%, rgba(247, 19, 19) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#3bade3 ', endColorstr='#ff357f ', GradientType=1 );
  height: 2px;
}

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: 2px solid rgb(202, 202, 202);
  border-radius: 50%;
  height: 18px;
  width: 18px;
  max-width: 80px;
  position: relative;
  bottom: 8px;
  background-color: #ececec;
  cursor: -webkit-grab;

  -webkit-transition: border 1000ms ease;
  transition: border 1000ms ease;
}

input[type=range]::-moz-range-thumb {
  -moz-appearance: none;
  border: 2px solid;
  border-radius: 50%;
  height: 18px;
  width: 18px;
  max-width: 80px;
  position: relative;
  bottom: 11px;
  background-color: #1d1c25;
  cursor: -moz-grab;
  -moz-transition: border 1000ms ease;
  transition: border 1000ms ease;
}

input[type=range]::-webkit-slider-thumb:active {
  cursor: -webkit-grabbing;
}

input[type=range]::-moz-range-thumb:active {
  cursor: -moz-grabbing;
}
/*END Range*/